<template>
  <div id="reimbursement">
    <transition name="operation">
      <div v-if="!funcStatus" class="func-operation">
        <div class="money-item pointer" @click="func(0)">
          <p class="item-name">申请</p>
        </div>
        <div class="money-item pointer" @click="func(1)">
          <p class="item-name">汇总</p>
        </div>
        <div class="funcList">
          <div class="money-item head pointer" @click="show">
            <p class="item-name left">历史</p>
            <div v-if="showHistory" class="back pointer left"></div>
          </div>
          <transition name="operation">
            <div v-if="showHistory" class="carousel-wrapper">
              <div v-for="(item, index) in historyList" v-bind:key="index" class="money-item pointer">
                <p class="date">{{item.date}}</p>
                <p class="text left">{{item.description}}</p>
                <div class="button right" :class="{'fail': item.status === 0}">
                  <p>{{result(item.status)}}</p>
                </div>
                <p class="account right">{{item.account}}元</p>
                <!--@click="read(index)"-->
              </div>
            </div>
          </transition>
        </div>
      </div>
    </transition>
    <router-view v-if="funcStatus"></router-view>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'reimbursement',
  data () {
    return {
      funcStatus: true,
      history: false,
      showHistory: false,
      historyList: [
        {description: '打车', date: '2018/01/02', account: 20, status: 1},
        {description: '芜湖-安庆', date: '2018/01/02', account: 63.5, status: 0},
        {description: '打车', date: '2018/01/02', account: 20, status: 1},
        {description: '安庆-池州', date: '2018/01/02', account: 17, status: 1},
        {description: '打车', date: '2018/01/02', account: 15, status: 0},
        {description: '补贴', date: '2018/01/02', account: 210, status: 1},
        {description: '池州-安庆', date: '2018/01/03', account: 17, status: 0},
        {description: '打车', date: '2018/01/03', account: 30, status: 1},
        {description: '安庆-合肥', date: '2018/01/08', account: 138, status: 1},
        {description: '打车', date: '2018/01/09', account: 10.2, status: 1}
      ]
    }
  },
  methods: {
    func: function (i) {
      this.funcStatus = true
      switch (i) {
        case 0:
          this.$router.push({path: '/user/reimbursement/application'})
          break
        case 1:
          this.$router.push({path: '/user/reimbursement/table'})
          break
      }
    },
    show: function () {
      this.showHistory = !this.showHistory
      if (this.showHistory) {
        var self = this
        setTimeout(function () {
          var h
          var temp = $('#user').height() - window.innerHeight
          if (!self.$root.adapt) {
            h = $('#main').height() + ($('.func-item').eq(0).height() + 17) * 3
          } else {
            h = $('#main').height() + $('.main-function').eq(0).height() + ($('.money-item').eq(0).height() + 17) * 2
          }
          if (temp > h) {
            temp = h
          }
          $('#app').animate({scrollTop: temp}, 300)
        }, 17)
      }
    },
    result: function (i) {
      if (i === 0) {
        return '未通过'
      } else {
        return '通 过'
      }
    }
  },
  mounted () {
    var self = this
    this.funcStatus = false
    this.$root.eventHub.$on('cancelReimbursement', function () {
      self.funcStatus = false
    })
    this.$root.eventHub.$on('Reimbursement', function () {
      self.funcStatus = true
    })
  }
}
</script>

<style scoped>
  .func-operation{
    height: auto;
    position: relative;
    z-index: 3;
  }
  .operation-enter-active{
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
  }
  .operation-enter{
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    -moz-transform: translate3d(0, -100%, 0);
    -ms-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  .money-item{
    overflow: hidden;
    background-color: white;
    border-bottom: 1px solid #f0f2f5;
    position: relative;
    z-index: 3;
  }
  .item-name{
    overflow: hidden;
  }
  .back{
    background: url("../../resource/assets/back2.png") 50% no-repeat;
    background-size: 50%;
  }
  .date{
    font-size: 16px;
    line-height: 20px;
  }
  .text, .account{
    font-size: 18px;
    height: 30px;
    line-height: 30px;
  }
  .text{
    max-width: calc(100% - 106px);
    overflow: hidden;
  }
  .button{
    width: 60px;
    height: 28px;
    border-radius: 4px;
    margin-left: 16px;
    cursor: pointer;
    clear: right;
    background: #3cb371;
  }
  .button p{
    width: 100%;
    text-align: center;
    font-size: 15px;
    line-height: 28px;
    color: white;
  }
  .fail{
    background: #dc143c;
  }

  @media only screen and (max-device-width: 767px) {
    .func-operation {
      width: calc(100% - 40px);
      margin: 0 20px;
    }
    .money-item{
      width: 100%;
      padding: 8px 0;
    }
    .item-name{
      font-size: 18px;
      line-height: 41px;
    }
    .text{
      padding-top: 4px;
    }
    .back{
      width: 40px;
      height: 40px;
      float: right;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .func-operation {
      width: calc(100% - 36px);
      margin: 0 18px;
    }
    .money-item{
      width: calc(100% - 40px);
      padding: 10px 20px;
    }
    .item-name{
      font-size: 18px;
      line-height: 39px;
    }
    .text{
      padding-top: 4px;
    }
    .back{
      margin-left: 10px;
    }
  }
</style>



